.wrapper {
  display: grid;
  gap: var(--small-distance-between);
  text-align: center;
  justify-items: center;
  font-size: var(--font-size-extra-small);
}

.linksContainer {
  font-size: var(--font-size-small);
}

.linksContainer > *:not(:last-child) {
  margin-right: var(--small-distance-between);
}

.image {
  border-radius: var(--small-border-radius);
}
