.wrapper {
  padding-bottom: clamp(50px, 10vh, 200px);
}

.title {
  margin-bottom: var(--large-distance-between);
  text-align: center;
}

.itemsList {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--default-distance-between);
}

